.menu {
  background-color: $c-accent-blue-dark-theme;
  height: 100vh;
  box-sizing: border-box;
  overflow-y: auto;

  &__content {
    padding: 18px 20px 0;
  }

  &__top {
    margin-bottom: 48px;
  }

  &__list {
    margin-bottom: 48px;
    list-style: none;
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__link {
    @include menu-link {
      font-size: 20px;
    }
  }

  &__subtitle {
    @include title-5 {
      opacity: 0.5;
    }
    margin-bottom: 16px;
  }
}
